import * as React from "react"
import { Link, graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"
import "../queries/allMarkdowns"
import TagsList from "../components/tagsList"

function ListIntro({ contentType, language }) {
  switch (language) {
    case 'en':
      switch (contentType) {
        case 'texts':
          return <><h1>Texts</h1><p>Some fleeting thoughts about the practice of digital media</p></>
        case 'projects':
          return <><h1>Projects</h1></>
        case 'Sketches':
          return <><h1>Sketches</h1><p>These experiments defend brevity and agility</p></>
      }
      break;
    case 'es':
      switch (contentType) {
        case 'texts':
          return <><h1>Textos</h1></>
        case 'projects':
          return <><h1>Projectos</h1></>
        case 'Sketches':
          return <><h1>Bosquejos</h1><p>Estos experimentos reivindican el juego y la levedad</p></>
      }

  }
  return null;
}

const List = ({ data, location, pageContext: { language, contentType } }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const posts = data.allMarkdownRemark.nodes

  return (
    <Layout location={location} title={siteTitle}>
      <header id="list-header"><ListIntro {...{ contentType, language }} /></header>
      <ol id="posts-list">
        {posts.filter(post => post.fields.language === language && post.fields.contentType === contentType).map(post => {
          const title = post.frontmatter.title || post.fields.slug

          return (
            <li key={post.fields.slug}>
              <article
                className="post-list-item"
                itemScope
                itemType="http://schema.org/Article"
              >
                <header>
                  <h2>
                    <Link to={post.fields.slug} itemProp="urlx">
                      <span itemProp="headline">{title}</span>
                    </Link>
                  </h2>
                  <div class="article-byline"><TagsList tags={post.frontmatter.tags} /></div>
                </header>
                <section>
                  <p
                    dangerouslySetInnerHTML={{
                      __html: post.frontmatter.description || post.excerpt,
                    }}
                    itemProp="description"
                  />
                </section>
              </article>
            </li>
          )
        })}
      </ol>
    </Layout>
  )
}

export default List;
export const Head = () => <><Seo title="All posts" /></>
export const pageQuery = graphql`
{
  site {
    ...SiteInformationFragment
  }
  allMarkdownRemark(sort: { frontmatter: { date: DESC } }) {
    nodes {
      ...MarkdownRemarkNodeFragment
    }
  }
}
`